.toolbarDiv {
  height: 50px;
  background: #12161C;
}
.mainLogoDiv {
  height: 100%;
  width: 150px;
  float: left;
}
.mainLogoDiv > img {
  width: 100%;
  height: 100%;
}
.loginDiv {
  float: right;
}
.hide {
  display: none;
}
.nav {
  float: left;
}
.loginBtn {
  line-height: 50px;
  margin-right: 16px;
}
.joinBtn {
  margin-right: 16px;
  min-width: 0px;
  -webkit-appearance: none;
  user-select: none;
  cursor: pointer;
  justify-content: center;
  background-color: rgb(240, 185, 11);
  height: 32px;
  background-image: linear-gradient(rgb(248, 209, 47) 0%, rgb(240, 185, 11) 100%);
  padding-left: 16px;
  padding-right: 16px;
  border-radius: 4px;
  border-style: none;
  font-size: 14px;
}
li {
  background: #12161c;
}
button {
  border: none;
  display: inline-block;
  padding: 8px 16px;
  vertical-align: middle;
  overflow: hidden;
  text-decoration: none;
  text-align: center;
  cursor: pointer;
}


